import React from "react";
import TopBar from "./sections/da/TopBar";
import TermsOfUseWeb from "./sections/da/TermsOfUseWeb";
import Footer2 from "./sections/da/Footer2";
import {isMobile} from "react-device-detect";
import {useMediaQuery} from "@material-ui/core";
import TopBarMobile from "./sections/da/TopBarMobile";

const TermsWeb = () => {

    const mobile = useMediaQuery("(max-width: 768px");
    return (
        <div className="landing">
            {isMobile || mobile ? <TopBarMobile/> : <TopBar/>}
            <TermsOfUseWeb/>
            <Footer2/>
        </div>
    );
};

export default TermsWeb;
